.profileText{
    text-align: center;
    margin-top: 100px;
    align-items: center;
    -webkit-backdrop-filter: blur(10px);
    backdrop-filter: blur(10px);
    background-color: hsla(200, 3%, 17%, 0.241);
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    margin: 120px auto auto;
    padding: 10px;
    width: 30%;
 }
 .profileText:hover{
     background-color:rgba(67, 70, 78, 0.293);
     --glow-color: rgb(217, 176, 255);
  --glow-spread-color: rgba(191, 123, 255, 0.781);
  --enhanced-glow-color: rgb(231, 206, 255);
  --btn-color: rgb(100, 61, 136);
 }
 .btnChnge{
     background-color: transparent;
     border:2px solid white;
     color: white;
     text-align: center;
     padding:1rem;
     border-radius: 0.35rem;
     cursor: pointer;
     width: 25vw;
     margin: 1rem;
     font-size: 1rem;
     font-weight: 500;
 }
 .btnChnge:hover{
     background-color: var(--white);
     color:var(--purple-grey);
     transition: all 0.2s;
 }
 @property --rotate {
     syntax: "<angle>";
     initial-value: 132deg;
     inherits: false;
   }
   
   :root {
     --card-height: 65vh;
     --card-width: calc(var(--card-height) / 1.5);
   }
   
