   .profileText:hover {
     color: rgb(255, 255, 255);
     background-image: linear-gradient(
         var(--rotate)
         , #5ddcff, hsl(225, 75%, 56%) 43%, #4e00c2);
     transition: color 1s;
 
     background-image: linear-gradient(
       var(--rotate)
       , #5ddcff, #3c67e3 43%, #4e00c2);
       
   }
   .profileText:hover:before, .profileText:hover:after {
     animation: none;
     opacity: 0;
   }
   
   
