.custom-btn{
    text-align: center;
    border:2px solid var(--white);
    padding:1rem;
    border-radius: 0.35rem;
    cursor: pointer;
    width: 70vw;
    margin: 1rem;
    font-size: 1rem;
    font-weight: 500;

}
.custom-btn:hover{
    background-color: var(--white);
    color:var(--purple-grey);
    transition: all 0.2s;
}